.projects-dark {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 80%;
  flex-wrap: wrap;
  background-color: lighten(#282c34, 4);
  border-radius: 1rem;
  padding: 2rem;
  transition: all 0.3s;

  .project {
    display: flex;
    position: relative;
    flex-direction: row;
    width: 100%;
    align-items: center;
    justify-content: space-around;
    padding: 2rem;
    transition: all 0.3s;
    &:not(:last-child) {
      border-bottom: #282c34 2px solid;
    }
  }
  .image-container {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    border-radius: 1rem;
    width: 16rem;
    height: 12rem;
  }
  .project-img {
    height: 12rem;
    width: 12rem;
    // overflow: hidden;

    background-color: #fff;
    border-radius: 1rem;
    //  border-radius: 50%;
  }
  .info-container {
    display: flex;
    border-radius: 1rem;
    justify-content: space-around;
  }
  .info-text {
    display: flex;
    flex-direction: column;

    max-width: 70%;
    font-size: 1.8rem;
    color: lighten(#282c34, 60);

    .title {
      width: 100%;
      color: #089eec;
      text-align: center;
      font-size: 2.4rem;
      font-weight: 400;
      margin-bottom: 1rem;
    }
  }
  .info-icons {
    display: flex;
    width: 12rem;
    justify-content: space-around;
    align-items: center;
  }
  .info-icon {
    color: white;
    font-size: 4rem;
    transition: all 0.3s;
    cursor: pointer;
    margin: 0 1rem;
    &:hover {
      transform: scale(1.1);
    }
  }
}
.projects_title-dark {
  width: 100%;
  font-size: 5rem;
  text-align: center;
  color: #089eec;
  font-weight: 400;
}
@media (max-width: 600px) {
  .projects-dark {
    width: 90%;
  }
  .info-icons {
    display: flex;
    flex-direction: column;
  }
  .info-icon {
    margin: 1rem 0;
  }
}
