  }
  .info-icon {
    color: #0077b5;
    font-size: 4rem;
    transition: all 0.3s;
    cursor: pointer;
    margin: 0 1rem;
    &:hover {
      transform: scale(1.1);
    }
  }
}
.projects_title-light {
